@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.5em;
  text-align: center;
  background: #16536B;
  color: #fff;
}
.App {
  max-width: 860px;
  margin: 40px auto;
}
button {
  background: none;
  border: 2px solid #fff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
  transition: 0.1s linear all;
}
button:hover {
  background: #EB5D31;
  color: #fff;
}
.card-grid {
  margin-top: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}

.card {
  position: relative;
  width: 150px;
  height: 150px;
  object-fit: contain;
}
@media screen and (min-width: 1367px) {
  .card{
    width: 180px;
    height: 180px;
  }
}

.card img {
  width: 100%;
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}
.card .front{
  transform: rotateY(90deg);
  position: absolute;
  transition: all ease-in 0.2s;
}
.flipped .front{
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

.card .cover{
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .cover{
  transform: rotateY(90deg);
  transition-delay: 0s;
}
